/* Button colors */
.blue {
    color: #fff;
    background-color: #337ab7;
    border-color: #2e6da4;
}

.orange {
    color: #fff;
    background-color: #f0ad4e;
    border-color: #eea236;
}

.delete {
    background-color: #a90329;
    color: #fff;
    border-color: #900323;
}

.delete:hover {
    color: #fff;
    background-color: #81021f;
    border-color: #540114
}

.success, .active {
    color: #FFF;
    background: #2ECC71;
}

.error, .deactivated {
    color: #FFF;
    background: #C0392B;
}
